.bbp-video-hero .hero-container.image-hero {
  padding: 3rem 0;
  min-height: 500px;
  display: flex;
  justify-content: start;
  align-items: center;
  background-position: center;
  background-size: cover;
}
.bbp-video-hero .hero-container.video-hero {
  height: 100%;
  overflow: hidden;
  position: relative;
}
.bbp-video-hero .hero-container.video-hero video {
  object-fit: cover;
  height: 100%;
  width: 100%;
  pointer-events: none;
}
.bbp-video-hero .hero-container.video-hero .content-holder {
  position: absolute;
  top: 50%;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  transform: translateY(-50%);
  max-width: 800px;
}
.bbp-video-hero .hero-container.video-hero .content {
  margin: 0 auto;
  padding: 2rem 1.5rem;
  color: #ffffff;
}